<template>
  <v-dialog
    :value="true"
    persistent
    width="500"
  >
    <v-card>
      <v-toolbar
        flat
        dense
      >
        <v-toolbar-title>
          Input Data Transaksi
        </v-toolbar-title>
      </v-toolbar>
      <v-card-text>
        <v-text-field
          type="number"
          min="0"
          label="Produksi"
          v-model.number="item.produksi"
        />
        <v-text-field
          type="number"
          min="0"
          label="Penjualan"
          v-model.number="item.penjualan"
        />
        <v-text-field
          type="number"
          min="0"
          label="Persediaan"
          v-model.number="item.persediaan"
        />

        <v-text-field
          type="number"
          min="0"
          label="Permintaan"
          v-model.number="item.permintaan"
        />
      </v-card-text>
      <v-card-actions>
        <v-btn
          flat
          dark
          color="green"
          @click="addTransaksi"
        >
          Simpan
        </v-btn>
        <v-btn
          flat
          dark
          color="pink"
          @click="$router.back()"
        >
          Batal
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
import Vue from "vue";
import defaults from "@/defaults";
import TransaksiAPI from "@/services/transaksi";
import evbus from "@/evbus";

export default Vue.extend({
  name: "AddTransaksi",
  data: () => ({
    item: { ...defaults.transaksi }
  }),
  methods: {
    async addTransaksi() {
      evbus.$emit("loading-on");
      try {
        let payload = { ...this.item };
        delete payload.tanggal;
        await TransaksiAPI.create(payload);
      } catch (err) {
        console.log(err);
      } finally {
        evbus.$emit("loading-off");
        this.$router.back();
      }
    }
  }
});
</script>

